import React, { useEffect, useRef, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { AiFillEye } from "react-icons/ai";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { BsCalendarFill } from "react-icons/bs";
import ReactDatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { getPrescriptionList } from "../../../app/features/patientPrescription/patientPrescription.slice";
import { useDispatch, useSelector } from "react-redux";
import MedicationModel from "./MedicationModel";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";
import PrescriptionPDF from "../../../components/Theme/PrescriptionPDF";
import moment from "moment";

export default function PatientDatatable() {
  const [show, setShow] = useState(false);
  const [selectedDate, setSelectedDate] = useState(null);
  const [viewPresctionData, setViewPresctionData] = useState();
  const [searchValue, setSearchValue] = useState();
  const [key, setKey] = useState(false);
  const printButtonRef = useRef(null);

  const dispatch = useDispatch();

  const { user } = useSelector((state) => state.auth);
  const { allPrescription } = useSelector(
    (state) => state?.patientPrescription
  );

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const printTimeOut = (data) => {
    setShow(false);
    setTimeout(function () {
      printButtonRef?.current?.click();
    }, 1000);
    // setSelectedDate(viewPresctionData?.medicineList);
  };
  const viewPrescription = (data) => {
    handleShow();
    setViewPresctionData(data);
  };

  const columns = [
    {
      dataField: "prescriptionId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "medicine",
      text: "Medication",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "allergies",
      text: "Allergy",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "quantity",
      text: "Quantity",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, data) => (
        <>
          <AiFillEye
            style={{ color: "blue", cursor: "pointer" }}
            onClick={() => viewPrescription(data)}
          />
        </>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "6",
        value: 10,
      },
    ],
  };

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
      search: searchValue ? searchValue : null,
      isPagination: true,
      isOnlineForm: key,
      prescribedDate: selectedDate
        ? moment(selectedDate).format("YYYY-MM-DD")
        : null,
    };
    dispatch(getPrescriptionList({ finalData }));
  }, [dispatch, user?.userId, searchValue, selectedDate, key]);

  return (
    <>
      <div className="usermanagement-mainclass">
        <Button
          variant="primary"
          onClick={() => setKey(false)}
          className={`Admin-Tabs-All me-2 mb-2 ${
            key === false ? "nav-btn-active" : "nav-btn"
          }`}
        >
          Appointment Prescriptions
        </Button>
        <Button
          variant="primary"
          onClick={() => setKey(true)}
          className={`Admin-Tabs-All me-2 mb-2 ${
            key === true ? "nav-btn-active" : "nav-btn"
          }`}
        >
          Online Form Prescriptions
        </Button>
      </div>
      <div className="Prescription-Main-Class patient-date-wrapper">
        <Card className="shadow-sm mt-4">
          <>
            <Row className="px-4 pt-3">
              <Col xl={3} sm={6}>
                <span className="d-flex align-self-center">
                  <Form.Control
                    onKeyDown={(e) => {
                      if (e.key === "Enter") {
                        setSearchValue(e.target.value);
                      }
                    }}
                    onChange={(e) => {
                      if (e.key === "Enter") {
                        setSearchValue(e.target.value);
                      }
                    }}
                    type="text"
                    placeholder="Search"
                    className="mb-3 search-field-spacing"
                    aria-label="Search"
                  />
                  <BiSearch size={22} className="searchbar-icon" />
                </span>
              </Col>
              <Col
                xl={9}
                sm={6}
                className="d-flex flex-wrap align-self-center justify-content-end pe-0"
              >
                <ReactDatePicker
                  selected={selectedDate}
                  onChange={(date) => setSelectedDate(date)}
                  dateFormat="dd MMM yyyy"
                  placeholderText="Date"
                  className="custom-field-picker invoice-cd-w px-2 mb-3"
                />
                <BsCalendarFill
                  size={18}
                  style={{
                    position: "relative",
                    right: "29px",
                    top: "15px",
                    color: "#999999",
                  }}
                />
              </Col>
            </Row>
            <span className="doctor-datatable">
              <BootstrapTable
                columns={columns}
                data={allPrescription ? allPrescription : []}
                keyField="id"
                id="bar"
                pagination={paginationFactory(paginateOptions)}
                // striped
                bordered={false}
                wrapperClasses="table-responsive"
              />
            </span>
          </>
        </Card>
        <MedicationModel
          viewPresctionData={viewPresctionData}
          handleClose={handleClose}
          show={show}
          onDownload={printTimeOut}
          setSelectedData={setSelectedDate}
        />
        <PrintComponents
          trigger={
            <button
              ref={printButtonRef}
              className="border-0 px-3 py-2 rounded-2 d-none"
            >
              <PiDownloadSimpleBold size={20} /> Download PDF
            </button>
          }
        >
          <PrescriptionPDF prescriptionData={viewPresctionData} />
        </PrintComponents>
      </div>
    </>
  );
}
